import React, { useEffect } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import moment from "moment";
// style imports
import useStyles from "../styles/muiStyles";
var sprintf = require("sprintf-js").sprintf;

const DesktopOrders = ({ orders }) => {
  const classes = useStyles();

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <div className={classes["stacked-cards"]}>
      <div className="card-group">
        <h3 className="card-title">{"Orders"}</h3>
        {orders.length > 0 ? (
          <TableContainer>
            <Table aria-label="Orders" className="orders">
              <TableHead>
                <TableRow>
                  <TableCell>{"Order No."}</TableCell>
                  <TableCell>{"Particulars"}</TableCell>
                  <TableCell>{"Date"}</TableCell>
                  <TableCell>{"Amount"}</TableCell>
                  <TableCell>{"Inv No."}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {orders &&
                  orders.map((order, index) => {
                    let particulars = "";
                    let duration = "";
                    if (order.order_type === "plan") {
                      duration = `${moment(order.start_date).format(
                        "D"
                      )} - ${moment(order.expiry_date).format("D MMM YYYY")}`;
                      duration += ` Plan Code: ${order.plan_id}`;
                    }
                    return (
                      <TableRow hover>
                        <TableCell>
                          {sprintf("%05s", order.order_number)}
                        </TableCell>
                        <TableCell>
                          {order.business_name}
                          {order.business_name && <br />}
                          {order.particulars}{" "}
                          {order.short_desc && order.short_desc}
                          <br />
                          {duration}
                        </TableCell>
                        <TableCell>
                          {moment(order.created_at).format("D MMM YYYY")}
                        </TableCell>
                        <TableCell>
                          {formatTwoPlace(order.paid_amount)}
                        </TableCell>
                        <TableCell>
                          {order.invoice_number
                            ? "ES" + sprintf("%05s", order.invoice_number)
                            : "pre-paid"}
                        </TableCell>
                      </TableRow>
                    );
                  })}
              </TableBody>
            </Table>
          </TableContainer>
        ) : (
          "No order available."
        )}
      </div>
    </div>
  );
};

export default DesktopOrders;
