import React, { Fragment, useEffect, useState } from "react";
import { Hidden } from "@material-ui/core";

import MobileOrders from "../../../Container/AllPayments/Orders/Mobile";
import DesktopOrders from "../../../Container/AllPayments/Orders/Desktop";

import Layout from "../Layout";
import { useToastMessage } from "../../../contexts";

import { getAllOrders } from "../../../api/Order";

interface IAllPaymentsOrders {
  orders: any;
}

const AllPaymentsOrders: React.FunctionComponent<IAllPaymentsOrders> = (
  props
) => {
  const { setErrorMessage } = useToastMessage();
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    loadOrders();
  }, []);

  const loadOrders = async () => {
    const res = await getAllOrders();
    if (!res.success) {
      setErrorMessage(res.message);
    } else {
      setOrders(res.payload);
    }
  };

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <MobileOrders orders={orders} />
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopOrders orders={orders} />
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default AllPaymentsOrders;
