import React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import moment from "moment";

import leftArrow from "../../../../assets/icons/leftArrow.svg";

// style imports
import useStyles from "../styles/muiStyles";
var sprintf = require("sprintf-js").sprintf;

const MobileOrders = ({ orders }) => {
  const classes = useStyles();

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };
  return (
    <div className={classes["mobile-settings"]}>
      <div className="header">
        <a href="/all-payments">
          <img alt="back" src={leftArrow} style={{ margin: "0 10px" }} />
        </a>
        <h1 className="title">{"All Payments"}</h1>
      </div>
      <div className="content">
        <div className="card-group">
          <h3 className="card-title">{"Orders"}</h3>
          {orders.length > 0 ? (
            <TableContainer>
              <Table aria-label="Orders" className="orders">
                <TableHead>
                  <TableRow>
                    <TableCell>{"Particulars"}</TableCell>
                    <TableCell>{"Date"}</TableCell>
                    <TableCell>{"Amount"}</TableCell>
                    <TableCell>{"Plan ID"}</TableCell>
                    <TableCell>{"Inv No."}</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {orders.map((order, index) => {
                    let particulars = "";
                    let start_expire = "";

                    if (order.order_type === "wallet") {
                      particulars = order.particulars;
                    }

                    if (order.order_type === "plan") {
                      start_expire = `${moment(order.start_date).format(
                        "D"
                      )} - ${moment(order.expiry_date).format("D MMM YYYY")}`;
                      start_expire += ` Plan Code: ${order.plan_id}`;
                    }
                    return (
                      <TableRow hover>
                        <TableCell>
                          {sprintf("%05s", order.order_number)}
                        </TableCell>
                        <TableCell>
                          {particulars && particulars}
                          {order.business_name}
                          <br />
                          {order.short_desc && order.short_desc}
                          <br />
                          {start_expire}
                        </TableCell>
                        <TableCell>
                          {moment(order.createdAt).format("D MMM YYYY")}
                        </TableCell>
                        <TableCell>
                          {order.currency === "INR" ? "" : "$"}{" "}
                          {formatTwoPlace(order.paid_amount)}
                        </TableCell>
                        <TableCell>
                          {order.invoice_number
                            ? "ES" + sprintf("%05s", order.invoice_number)
                            : "pre-paid"}
                        </TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </TableContainer>
          ) : (
            "No order available."
          )}
        </div>
      </div>
    </div>
  );
};

export default MobileOrders;
